sourcesContent":["/*\r\n  Reset is a mix of:\r\n  - https://meyerweb.com/eric/tools/css/reset/\r\n  - https://piccalil.li/blog/a-modern-css-reset/\r\n*/\r\nhtml, body, div, span, applet, object, iframe,\r\nh1, h2, h3, h4, h5, h6, p, blockquote, pre,\r\na, abbr, acronym, address, big, cite, code,\r\ndel, dfn, em, img, ins, kbd, q, s, samp,\r\nsmall, strike, strong, sub, sup, tt, var,\r\nb, u, i, center,\r\ndl, dt, dd, ol, ul, li,\r\nfieldset, form, label, legend,\r\ntable, caption, tbody, tfoot, thead, tr, th, td,\r\narticle, aside, canvas, details, embed,\r\nfigure, figcaption, footer, header, hgroup,\r\nmenu, nav, output, ruby, section, summary,\r\ntime, mark, audio, video {\r\n\tmargin: 0;\r\n\tpadding: 0;\r\n\tborder: 0;\r\n\tfont-size: 100%;\r\n\tfont: inherit;\r\n\tvertical-align: baseline;\r\n}\r\n/* HTML5 display-role reset for older browsers */\r\narticle, aside, details, figcaption, figure,\r\nfooter, header, hgroup, menu, nav, section {\r\n\tdisplay: block;\r\n}\r\nbody {\r\n\tline-height: 1;\r\n}\r\nol, ul {\r\n\tlist-style: none;\r\n}\r\nblockquote, q {\r\n\tquotes: none;\r\n}\r\nblockquote:before, blockquote:after,\r\nq:before, q:after {\r\n\tcontent: '';\r\n\tcontent: none;\r\n}\r\ntable {\r\n\tborder-collapse: collapse;\r\n\tborder-spacing: 0;\r\n}\r\n\r\n/* Box sizing rules */\r\n*,\r\n*::before,\r\n*::after {\r\n  box-sizing: border-box;\r\n}\r\n\r\n/* Inherit fonts for inputs and buttons */\r\ninput,\r\nbutton,\r\ntextarea,\r\nselect {\r\n  font: inherit;\r\n}\r\n","/*\r\n  Space\r\n  getThemeProperty(layoutSpace) variable is defined in _fw-variables.scss\r\n  Used for consistent margins and gutters throughout the defined content\r\n*/\r\n.fw-space,\r\n.space{\r\n  padding: getThemeProperty(layoutSpace);\r\n  &.double{\r\n    padding: getThemeProperty(layoutSpace) * 2;\r\n  }\r\n  &.triple{\r\n    padding: getThemeProperty(layoutSpace) * 3;\r\n  }\r\n  &.one-forth{\r\n    padding: getThemeProperty(layoutSpace) * 0.25;\r\n  }\r\n  &.half{\r\n    padding: getThemeProperty(layoutSpace) / 2;\r\n  }\r\n  &.three-forths{\r\n    padding: getThemeProperty(layoutSpace) * 0.75;\r\n  }\r\n\t&.noleft{\r\n\t\tpadding-left: 0;\r\n\t}\r\n\t&.noright{\r\n\t\tpadding-right: 0;\r\n\t}\r\n\t&.halfleft{\r\n\t\tpadding-left: getThemeProperty(layoutSpace) / 2;\r\n\t}\r\n\t&.halfright{\r\n\t\tpadding-right: getThemeProperty(layoutSpace) / 2;\r\n\t}\r\n\t&.notop{\r\n\t\tpadding-top: 0;\r\n\t}\r\n\t&.nobottom{\r\n\t\tpadding-bottom: 0;\r\n\t}\r\n\t&.halftop{\r\n\t\tpadding-top: getThemeProperty(layoutSpace) / 2;\r\n\t}\r\n\t&.halfbottom{\r\n\t\tpadding-bottom: getThemeProperty(layoutSpace) / 2;\r\n\t}\r\n}\r\n","/*\r\n  Theme Properties\r\n*/\r\n$default-theme-properties: (\r\n  // Colors\r\n  primaryColor: #999999,\r\n  secondaryColor: #000000,\r\n  tertiaryColor: #cccccc,\r\n  backgroundColorLight: #ffffff,\r\n  backgroundColorDark: #333333,\r\n  textColorLight: #ffffff,\r\n  textColorDark: #333333,\r\n\r\n  // Fonts\r\n  fontHeader: \"Montserrat\",\r\n  fontText: \"Roboto\",\r\n  fontIcon: \"Font Awesome 5 Pro\",\r\n\r\n  // Layout\r\n  layoutSpace: 20px,\r\n  layoutContainerWdth: 1366px,\r\n\r\n  // Breakpoints\r\n  breakpointDesktop: (\r\n    desktop: 1366px\r\n  ),\r\n  breakpointResponsive: (\r\n    desktopsmall: 1366px,\r\n    tablet: 1024px,\r\n    phone : 600px\r\n  )\r\n);\r\n\r\n/*\r\n  getThemeProperty\r\n  Returns the property value requested, either with the $theme-properties or the passed in $properties\r\n  - $key - The map element to lookup\r\n  - $properties - a map of properties to use instead of the default $theme-properties\r\n  - ex:\r\n    `color: getThemeProperty(primaryColor)`;\r\n*/\r\n@function getThemeProperty($key, $properties:()) {\r\n  @if(length($properties) > 0){ // Check to see if any properties were passed in\r\n    @if(map-get($properties, $key)){ // Check if the key exists in the properties\r\n      @return map-get($properties, $key);\r\n    }@else{\r\n      @return getThemeProperty($key); // Fall back to the theme properties\r\n    }\r\n  }@else{\r\n    @if(map-get($theme-properties, $key)){\r\n      @return map-get($theme-properties, $key);\r\n    }@else{\r\n      @return #00ff00; // The key was not found, so we return this color as an error\r\n    }\r\n  }\r\n}\r\n\r\n/*\r\n  Recursive Map Merge\r\n*/\r\n@function recursive-map-merge($parent-map, $child-map) {\r\n  $result: $parent-map;\r\n  @each $key, $value in $child-map {\r\n    @if (not map-has-key($result, $key)) or (type-of(map-get($result, $key)) != type-of($value)) or (not (type-of(map-get($result, $key)) == map and type-of($value) == map)) {\r\n      $result: map-merge($result, ($key: $value));\r\n    }\r\n    @else {\r\n      $result: map-merge($result, ($key: recursive-map-merge(map-get($result, $key), $value)));\r\n    }\r\n  }\r\n  @return $result;\r\n}\r\n\r\n// If $theme-properties is set, lets merge into our defaults\r\n$theme-properties: $default-theme-properties !default;\r\n@if($theme-properties){\r\n  $theme-properties: recursive-map-merge($default-theme-properties, $theme-properties);\r\n}\r\n","/*\r\n  Flex\r\n  Classes with default flex properties defined and inline flex helpers\r\n*/\r\n.flex-container{\r\n  display: -webkit-box;\r\n  display: -moz-box;\r\n  display: -ms-flexbox;\r\n  display: -webkit-flex;\r\n  display: flex;\r\n  flex-flow: row wrap;\r\n\tjustify-content: flex-start;\r\n  align-content: flex-start;\r\n}\r\n.flex-image {\r\n  width: 100%;\r\n  height: auto;\r\n}\r\n.flex-iframe{\r\n  width: 100%;\r\n  iframe{\r\n    width: 100%;\r\n  }\r\n}\r\n.flex-vertical-top {\r\n  align-items: flex-start;\r\n}\r\n.flex-vertical-center {\r\n  align-items: center;\r\n}\r\n.flex-vertical-bottom {\r\n  align-items: flex-end;\r\n}\r\n.flex-vertical-stretch {\r\n  align-items: stretch;\r\n}\r\n.flex-horizontal-left {\r\n  justify-content: flex-start;\r\n}\r\n.flex-horizontal-center {\r\n  justify-content: center;\r\n}\r\n.flex-horizontal-right {\r\n  justify-content: flex-end;\r\n}\r\n","/*\r\n  DEFAULT DESKTOP\r\n  If no breakpoints are defined, these will be the fallback width percentages\r\n*/\r\n.desktop-half{\r\n  width: 0.5%;\r\n  min-height: 1px;\r\n}\r\n@for $i from 1 through 100 {\r\n  .desktop-#{$i} {\r\n    width : round(percentage($i/100));\r\n  }\r\n}\r\n.desktop-hide{\r\n  display: none;\r\n}\r\n.desktop-auto{\r\n  width: auto;\r\n}\r\n.desktop-show{\r\n  display: inherit;\r\n}\r\n/*\r\n  DESKTOP BREAKPOINTS\r\n  Loop through each desktop defined breakpoint\r\n*/\r\n@each $label, $breakpoint in getThemeProperty(breakpointDesktop){\r\n  @media screen and (min-width: $breakpoint) {\r\n    .#{$label}-half{\r\n      width: 0.5%;\r\n      min-height: 1px;\r\n    }\r\n    @for $i from 1 through 100 {\r\n      .#{$label}-#{$i} {\r\n        width : round(percentage($i/100));\r\n      }\r\n    }\r\n    .#{$label}-auto{\r\n      width: auto;\r\n    }\r\n    .#{$label}-hide{\r\n      display: none;\r\n    }\r\n    .#{$label}-show{\r\n      display: inherit;\r\n    }\r\n  }\r\n}\r\n/*\r\n  RESPONSIVE BREAKPOINTS\r\n  Simply out puts classes with a width set from 1-100 for each breakpoint defined.\r\n*/\r\n@each $label, $breakpoint in getThemeProperty(breakpointResponsive){\r\n  @media handheld, only screen and (max-width: $breakpoint) {\r\n    .#{$label}-half{\r\n      width: 0.5%;\r\n      min-height: 1px;\r\n    }\r\n    @for $i from 1 through 100 {\r\n      .#{$label}-#{$i} {\r\n        width: round(percentage($i/100));\r\n      }\r\n    }\r\n    .#{$label}-auto{\r\n      width: auto;\r\n    }\r\n    .#{$label}-hide{\r\n      display: none;\r\n    }\r\n    .#{$label}-show{\r\n      display: inherit;\r\n    }\r\n    /* Flex Container Direction */\r\n    .flex-container{\r\n      &.#{$label}-ltr{\r\n        flex-direction: row;\r\n      }\r\n      &.#{$label}-rtl{\r\n        flex-direction: row-reverse;\r\n      }\r\n      &.#{$label}-ttb{\r\n        flex-direction: column;\r\n      }\r\n      &.#{$label}-btt{\r\n        flex-direction: column-reverse;\r\n      }\r\n    }\r\n    /* SPACING RESET */\r\n    .space{\r\n      &.#{$label}-space-reset{\r\n        padding: getThemeProperty(layoutSpace);\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n","/*\r\n These are a part of the piccalil modern reset file\r\n  - https://piccalil.li/blog/a-modern-css-reset/\r\n*/\r\n\r\n/* Set core root defaults */\r\nhtml:focus-within {\r\n  scroll-behavior: smooth;\r\n}\r\n\r\n/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */\r\n@media (prefers-reduced-motion: reduce) {\r\n  html:focus-within {\r\n   scroll-behavior: auto;\r\n  }\r\n\r\n  *,\r\n  *::before,\r\n  *::after {\r\n    animation-duration: 0.01ms !important;\r\n    animation-iteration-count: 1 !important;\r\n    transition-duration: 0.01ms !important;\r\n    scroll-behavior: auto !important;\r\n  }\r\n}\r\n","body{\r\n  background: getThemeProperty(backgroundColorLight);\r\n}\r\n","hr{\r\n  width: 100%;\r\n  color: getThemeProperty(primaryColor);\r\n}\r\n","/*\r\n  Image\r\n  Generic image classes\r\n  These classes are set from silverstripe html editor\r\n*/\r\nimg{\r\n  &.left{\r\n  \tfloat: left;\r\n  \tpadding-right: getThemeProperty(layoutSpace);\r\n  \tpadding-bottom: getThemeProperty(layoutSpace);\r\n  \tmax-width: 100%;\r\n    height: auto;\r\n  }\r\n  &.right{\r\n  \tfloat: right;\r\n  \tpadding-left: getThemeProperty(layoutSpace);\r\n  \tpadding-bottom: getThemeProperty(layoutSpace);\r\n  \tmax-width: 100%;\r\n    height: auto;\r\n  }\r\n  &.center{\r\n  \tdisplay: block;\r\n  \tmargin: 0 auto;\r\n  \ttext-align: center;\r\n  \tpadding: getThemeProperty(layoutSpace) 0;\r\n  \tmax-width: 100%;\r\n    height: auto;\r\n  }\r\n  &.leftAlone{\r\n  \tdisplay: block;\r\n  \ttext-align: left;\r\n  \tpadding: getThemeProperty(layoutSpace) 0;\r\n  \tmax-width: 100%;\r\n    height: auto;\r\n  }\r\n}\r\n@media handheld, only screen and (max-width: map-get(getThemeProperty(breakpointResponsive), tablet)) {  img{\r\n    &.left,\r\n    &.right,\r\n    &.center,\r\n    &.leftAlone{\r\n    \tmax-width: 100%;\r\n      height: auto;\r\n    }\r\n  }\r\n}\r\n@media handheld, only screen and (max-width: map-get(getThemeProperty(breakpointResponsive), phone)) {\r\n  img{\r\n    &.left,\r\n    &.right,\r\n    &.center,\r\n    &.leftAlone{\r\n    \twidth: 100%;\r\n      height: auto;\r\n    }\r\n  }\r\n}\r\n","/*\r\n  Link\r\n  Default link styles\r\n*/\r\na{\r\n  color: getThemeProperty(primaryColor);\r\n  text-decoration: underline;\r\n  &:link{\r\n    color: getThemeProperty(primaryColor);\r\n  }\r\n  &:visited{\r\n    color: getThemeProperty(primaryColor);\r\n  }\r\n  &:hover{\r\n    text-decoration: none;\r\n  }\r\n  &:active{\r\n    color: getThemeProperty(primaryColor);\r\n  }\r\n}\r\n","table{\r\n  width: 100%;\r\n  th,\r\n  td{\r\n    padding: 10px;\r\n  }\r\n  thead{\r\n    position: sticky;\r\n    top: 0;\r\n    tr{\r\n      table-layout: fixed;\r\n      width: 100%;\r\n    }\r\n    th{\r\n      font-weight: 700;\r\n      text-align: left;\r\n      padding: 10px 15px;\r\n      font-weight: 700;\r\n      font-size: 1.25em;\r\n      line-height: 1em;\r\n    }\r\n  }\r\n  tbody{\r\n    table-layout: fixed;\r\n    tr{\r\n      &:nth-child(odd){\r\n        background: #f3f3f5;\r\n        border-radius: 3px;\r\n      }\r\n    }\r\n    td{\r\n      padding: 10px 15px;\r\n    }\r\n  }\r\n  tfoot{\r\n    font-size: 0.8em;\r\n    line-height: 1em;\r\n    td{\r\n    }\r\n  }\r\n}\r\n@media handheld, only screen and (max-width: map-get(getThemeProperty(breakpointResponsive), tablet)) {\r\n}\r\n@media handheld, only screen and (max-width: map-get(getThemeProperty(breakpointResponsive), phone)) {\r\n  table{\r\n    font-size: 0.8em;\r\n    th,\r\n    td{\r\n      padding: 5px;\r\n    }\r\n  }\r\n}\r\n","/*\r\n  Ordered & Un-ordered Column List\r\n*/\r\nul,\r\nol{\r\n  &.two-column-list{\r\n    columns: 2;\r\n    column-gap: 40px;\r\n  }\r\n  &.three-column-list{\r\n    columns: 3;\r\n    column-gap: 40px;\r\n  }\r\n  &.four-column-list{\r\n    columns: 4;\r\n    column-gap: 40px;\r\n  }\r\n  &.five-column-list{\r\n    columns: 5;\r\n    column-gap: 40px;\r\n  }\r\n}\r\nol{\r\n  li{\r\n    margin-bottom: 20px;\r\n    &::marker{\r\n      font-weight: 700;\r\n      line-height: 1;\r\n      font-size: 1.2em;\r\n    }\r\n  }\r\n}\r\n@media handheld, only screen and (max-width: map-get(getThemeProperty(breakpointResponsive), phone)) {\r\n  ul,\r\n  ol{\r\n    &.two-column-list,\r\n    &.three-column-list,\r\n    &.four-column-list,\r\n    &.five-column-list{\r\n      columns: 1;\r\n    }\r\n  }\r\n}\r\n","@import 'font-size-mixin';\r\n\r\n/*\r\n\tTYPOGRAPHY\r\n\tDefine all basic html tags. These can be overridden in but this should be the base.\r\n*/\r\nbody {\r\n\tfont: 400 1rem/1rem getThemeProperty(fontText);\r\n}\r\n/* H1 */\r\n$h1-font-sizes: (\r\n  desktop: (2.5em, 1),\r\n  tablet: (2.5em, 1),\r\n  phone: (2.5em, 1)\r\n);\r\nh1{\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontHeader);\r\n  font-weight: 700;\r\n\t@include font-size($h1-font-sizes);\r\n}\r\n/* H2 */\r\n$h2-font-sizes: (\r\n  desktop: (2.250em, 1),\r\n  tablet: (2.250em, 1),\r\n  phone: (2.250em, 1)\r\n);\r\nh2{\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontHeader);\r\n  font-weight: 700;\r\n\t@include font-size($h2-font-sizes);\r\n}\r\n/* H3 */\r\n$h3-font-sizes: (\r\n  desktop: (1.500em, 1),\r\n  tablet: (1.500em, 1),\r\n  phone: (1.500em, 1)\r\n);\r\nh3{\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontHeader);\r\n  font-weight: 700;\r\n\t@include font-size($h3-font-sizes);\r\n}\r\n/* H4 */\r\n$h4-font-sizes: (\r\n  desktop: (1.313em, 1),\r\n  tablet: (1.313em, 1),\r\n  phone: (1.313em, 1)\r\n);\r\nh4{\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontHeader);\r\n\t@include font-size($h4-font-sizes);\r\n}\r\n/* H5 */\r\n$h5-font-sizes: (\r\n  desktop: (1.313em, 1),\r\n  tablet: (1.313em, 1),\r\n  phone: (1.313em, 1)\r\n);\r\nh5{\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontHeader);\r\n\t@include font-size($h5-font-sizes);\r\n}\r\n/* H6 */\r\n$h6-font-sizes: (\r\n  desktop: (1.125em, 1),\r\n  tablet: (1.125em, 1),\r\n  phone: (1.125em, 1)\r\n);\r\nh6{\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontHeader);\r\n\t@include font-size($h6-font-sizes);\r\n}\r\n/* P */\r\n$p-font-sizes: (\r\n  desktop: (1em, 1.4),\r\n  tablet: (1em, 1.4),\r\n  phone: (1em, 1.4)\r\n);\r\np {\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontText);\r\n  @include font-size($p-font-sizes);\r\n}\r\n/* UL, OL */\r\nul,ol {\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontText);\r\n  @include font-size($p-font-sizes);\r\n}\r\nul{\r\n\tlist-style: disc outside;\r\n}\r\nol{\r\n\tlist-style: decimal outside;\r\n}\r\n/* Form */\r\ninput,\r\nselect,\r\ntextarea{\r\n  color: getThemeProperty(textColorDark);\r\n  font-family: getThemeProperty(fontText);\r\n  @include font-size($p-font-sizes);\r\n}\r\n/**/\r\nstrong, b {\r\n\tfont-weight: 700;\r\n}\r\nem, i {\r\n\tfont-style: italic;\r\n}\r\nsup{\r\n  vertical-align: super;\r\n  font-size: smaller;\r\n}\r\nsub{\r\n  vertical-align: sub;\r\n  font-size: smaller;\r\n}\r\n.text-center{\r\n  text-align: center;\r\n}\r\n.text-left{\r\n  text-align: left;\r\n}\r\n.text-right{\r\n  text-align: right;\r\n}\r\n/**/\r\n.large-text{\r\n  font-size: 1.5em;\r\n}\r\n.text-white{\r\n  color: #ffffff;\r\n}\r\n","/*\r\n  FONT SIZE\r\n*/\r\n@mixin font-size($fs-map, $fs-breakpoints: getThemeProperty(breakpointResponsive)) {\r\n  @each $fs-breakpoint, $fs-font-size in $fs-map {\r\n    @if $fs-breakpoint == desktop {\r\n      @include make-font-size($fs-font-size);\r\n    }\r\n    @else {\r\n      // If $fs-font-size is a key that exists in\r\n      // $fs-breakpoints, use the value\r\n      @if map-has-key($fs-breakpoints, $fs-breakpoint) {\r\n        $fs-breakpoint: map-get($fs-breakpoints, $fs-breakpoint);\r\n      }\r\n      @media screen and (max-width: $fs-breakpoint) {\r\n        @include make-font-size($fs-font-size);\r\n      }\r\n    }\r\n  }\r\n}\r\n// Utility function for mixin font-size\r\n@mixin make-font-size($fs-font-size) {\r\n  // If $fs-font-size is a list, include\r\n  // both font-size and line-height\r\n  @if type-of($fs-font-size) == \"list\" {\r\n    font-size: nth($fs-font-size, 1);\r\n    @if (length($fs-font-size) > 1) {\r\n      line-height: nth($fs-font-size, 2);\r\n    }\r\n  }\r\n  @else {\r\n    font-size: $fs-font-size;\r\n  }\r\n}\r\n","/*\r\n  BUTTON\r\n  Generic button class can be used as a class on an element or as a mixin in scss.\r\n*/\r\n\r\n// Default Button Properties\r\n$default-component-button-properties:(\r\n  backgroundColor: getThemeProperty(backgroundColorDark),\r\n  textColor: getThemeProperty(textColorLight),\r\n  backgroundHoverColor: getThemeProperty(backgroundColorLight),\r\n  textHoverColor: getThemeProperty(textColorDark),\r\n  padding: 10px 40px,\r\n  margin: 5px 0,\r\n  borderRadius: 5px\r\n);\r\n// If $component-button-properties is set, lets merge into our defaults\r\n$component-button-properties: $default-component-button-properties !default;\r\n@if($component-button-properties){\r\n  $component-button-properties: map-merge($default-component-button-properties, $component-button-properties);\r\n}\r\n\r\n// Button Mixin\r\n@mixin button-input-style() {\r\n  background-color: getThemeProperty(backgroundColor, $component-button-properties);\r\n  border-radius: getThemeProperty(borderRadius, $component-button-properties);\r\n  color: getThemeProperty(textColor, $component-button-properties);\r\n  display: inline-block;\r\n  position: relative;\r\n  text-decoration: none;\r\n  padding: getThemeProperty(padding, $component-button-properties);\r\n  margin: getThemeProperty(margin, $component-button-properties);\r\n  border: none;\r\n  cursor: pointer;\r\n  -webkit-appearance: none;\r\n  -webkit-transition: background-color 0.25s ease-out;\r\n  -moz-transition: background-color 0.25s ease-out;\r\n  -o-transition: background-color 0.25s ease-out;\r\n  transition: background-color 0.25s ease-out;\r\n  &:link,\r\n  &:visited,\r\n  &:hover,\r\n  &:active{\r\n    color: getThemeProperty(textColor, $component-button-properties);\r\n  }\r\n  &:hover{\r\n    background-color: getThemeProperty(backgroundHoverColor, $component-button-properties);\r\n    color: getThemeProperty(textHoverColor, $component-button-properties);\r\n  }\r\n}\r\n\r\n// Applied to `a` elements with the `button` class\r\na.button{\r\n  @include button-input-style;\r\n}\r\n\r\n/*\r\n  To add onto the button, you can define the following in your local\r\n  a.button{\r\n    &.super-custom-button{\r\n      background-color: #fbff00;\r\n      color: #333333;\r\n    }\r\n  }\r\n*/\r\n\r\n","/*\r\n  CONTAINER\r\n  Site wide generic container class. Will center all content within it's max-width\r\n*/\r\n.container,\r\n.fw-container{\r\n  max-width: getThemeProperty(layoutContainerWdth);\r\n  margin: 0 auto;\r\n}\r\n","// Default form radio Properties\r\n$default-component-form-radio-properties:(\r\n  backgroundColor: getThemeProperty(backgroundColorLight),\r\n  checkedIcon: \"\\f192\",\r\n  checkedTextColor: getThemeProperty(textColorDark),\r\n  notCheckedIcon: \"\\f111\",\r\n  notCheckedTextColor: getThemeProperty(textColorDark),\r\n  iconFontFamily: getThemeProperty(fontIcon),\r\n);\r\n// If $component-form-radio-properties is set, lets merge into our defaults\r\n$component-form-radio-properties: $default-component-form-radio-properties !default;\r\n@if($component-form-radio-properties){\r\n  $component-form-radio-properties: map-merge($default-component-form-radio-properties, $component-form-radio-properties);\r\n}\r\n\r\n.form-modern{\r\n  fieldset.field div, .field.optionset div, ul.op